/**
 *  Modal
 *  Modal Component of TOL
 */

import { Link } from 'react-router'
import DelayButton from './DelayButton'
import {M} from '../stagesModel';


export default class Modal extends React.Component{
  constructor(props){
    super(props)
  }

  getNextStage(){
    return Number(this.props.params.stage) + 1
  }

  saveUser(e){
    e.preventDefault();

    let _name       = e.target[0].value;
    let _gender     = e.target[1].value;
    let _cognitive  = e.target[2].value;
    let _group      = e.target[3].value;

    let _user = {
      name: _name,
      gender:  _gender,
      cognitive: _cognitive,
      group: _group
    }

    __tol__.setState({
      user: _user
    })

    __tol__.user = _user;

    // TODO: use API to remove this hardcode
    location.hash = "#/instruction/2"
  }

  /**
   * getDataTable
   * calculate recorded data and generate table.
   */
  getDataTable(){
    if(this.props.type !== "instructionend") return;

    let _moveScore    = 0,
        _correctScore = 0,
        _minMoves     = M,
        _actualMoves  = __tol__.state.moves


    _minMoves.forEach((min, index) => {
      // except stage TEST
      if(index == 0) return;
      let act = _actualMoves[index];

      console.log(`${index} : ${min}`);
      console.log(`${index} : ${act}`);
      if (!act) act = 0;
      let sco = act - min;
      console.log(`${index} : ${sco}`);

      // correct score
      if(min == act) _correctScore++

      // move score
      _moveScore += sco;
    })

    return (
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Gender</th>
            <th>Cognitive</th>
            <th>Group</th>
            <th>MoveScore</th>
            <th>CorrectScore</th>
            <th>RuleVio</th>
            <th>TimeVio</th>
            <th>InitTime</th>
            <th>ExeTime</th>
            <th>P2STime</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{__tol__.state.user.name}</td>
            <td>{__tol__.state.user.gender}</td>
            <td>{__tol__.state.user.cognitive}</td>
            <td>{__tol__.state.user.group}</td>
            <td>{_moveScore}</td>
            <td>{_correctScore}</td>
            <td>{__tol__.state.violation}</td>
            <td>{__tol__.state.timeViolation}</td>
            <td>{__tol__.state.initTime}</td>
            <td>{__tol__.state.exeTime}</td>
            <td>
              {
                Number(__tol__.state.initTime)+
                Number(__tol__.state.exeTime)
              }
            </td>
          </tr>
        </tbody>
      </table>
    )
  }

  render(){
    if(!this.props.type) return <div className="modal cached" />;

    let modalMap = {
      "instruction1": (
        <div>
          <p>
            
          </p>
          <p>
            
          </p>
          <form onSubmit={this.saveUser}>
            <input required type="number" placeholder=""/>
            <select>
              <option></option>
              <option></option>
            </select>
            <select>
              <option></option>
              <option></option>
              <option></option>
            </select>
            <select>
              <option></option>
              <option></option>
            </select>
            <button type="submit"> Done! </button>
          </form>
        </div>
      ),
      "instruction2": (
        <div>
          <p>
            
          </p>
          <p>
            <br />
            
          </p>
          <p>
            <br />
            <em></em>
          </p>
          <Link to="/instruction/3">
            <button> Got it! </button>
          </Link>
        </div>
      ),
      "instruction3": (
        <div>
          <p>
            <em></em>
          </p>
          <p>
            <br/>
            
          </p>
          <Link to="/A/0">
            <button></button>
          </Link>
        </div>
      ),
      "testCompleted": (
        <div>
          <p>
            
          </p>
          <p></p>
          <Link to={`/A/${this.getNextStage()}`}>
            <button></button>
          </Link>
        </div>
      ),
      "stageCompleted": (
        <div>
          <p>
            
          </p>
          <Link to={`/A/${this.getNextStage()}`}>
            <button></button>
          </Link>
        </div>
      ),
      "halfCompleted": (
        <div>
          <p>
            
          </p>
          <p>
            
          </p>
          <Link to="/A/11">
            <DelayButton delay={3000}></DelayButton>
          </Link>
        </div>
      ),
      "gameCompleted": (
        <div>
          <p>
            
            
          </p>
          <p>
            
          </p>
          <Link to="/instruction/end">
            <DelayButton delay={2000}></DelayButton>
          </Link>
        </div>
      ),
      "instructionend": (
        <div className="data-table">
          <h3></h3>
          {this.getDataTable()}
          <Link to="/instruction/1">
            <DelayButton delay={1000}></DelayButton>
          </Link>
        </div>
      ),
    }

    return (
      <div className="modal">
        {modalMap[this.props.type]}
      </div>
    )
  }
}



